.news-btn {
	position: absolute;
	display: block;
	right: 15px;
	bottom: 10px;
	background: none;
	color: $blue;
	font-family: $gotham500;
	font-weight: 500;
	font-size: 12px;
	transition: background 0.3s, color 0.3s;
	&:hover {
		color: $orange; } }



.news {
	height: 100%;
	background-color: $color_main_bg;
	&__container {
		font-size: 16px;
		padding: 15px 115px 0; } }

.news__title {
	display: flex;
	flex-direction: column;
	position: relative;
	margin-bottom: 20px; }

.bread-crams {
	.title-step {
		margin-bottom: 0; } }



.news__menu__dropdown {
	position: relative;
	right: 0;
	bottom: 10px;
	.news__dropdown__box {
		position: absolute;
		right: 0;
		z-index: 6;
		.dropdown-toggle {
			font-family: $gotham500;
			font-size: 13px;
			font-weight: 500;
			color: $blue;
			background: none;
			.icon-arrow-down {
				width: 10px;
				height: 6px;
				color: $blue_light;
				padding-bottom: 2px; } }
		.news-directions {
			background-color: $color_white_blue;
			padding: 5px 10px;
			display: none;
			.categories {
				display: block;
				font-family: $gotham500;
				font-size: 13px;
				font-weight: 500;
				color: $blue;
				background: none;
				padding-bottom: 5px;
				transition: color 0.3s;
				&:hover {
					color: $orange; } } } } }


.news__row__first {
	display: flex;
	justify-content: space-between;
	margin-top: 40px;
	font-size: 12px;
	font-weight: 500;
	color: $blue;

	.news__left__block {
		position: relative;
		flex: 5 1 22%;
		background-color: #fff;
		img {
			width: 100%; }
		span {
			display: block;
			padding: 8px 20px 0;
			margin-bottom: 40px; } }


	.news__center__block {
		flex: 1 1 55%;
		margin: 0 20px;
		position: relative;
		background-color: #fff;
		img {
			width: 100%;
			height: 100%; }
		span {
			display: block;
			position: absolute;
			bottom: 50px;
			left: 0;
			padding: 0 15px; } }

	.gradient {
		position: absolute;
		width: 100%;
		height: 100%;
		background: linear-gradient(to top, rgba(255, 255, 255, 0.9) 20%, rgba(0, 0, 0, 0.1) 55%); }

	.news__right__block {
		position: relative;
		flex: 2 1 22%;
		background: #fff;
		img {
			width: 100%; }
		span {
			display: block;
			padding: 8px 20px 0;
			margin-bottom: 40px; } } }

.news__row__second {
	display: flex;
	justify-content: space-between;
	margin-top: 20px;
	font-size: 12px;
	font-weight: 500;
	color: $blue;

	.news__block {
		position: relative;
		flex: 1 1 25%;
		background-color: #fff;
		img {
			width: 100%; }
		span {
			display: block;
			padding: 8px 20px 0;
			margin-bottom: 40px; }

		&:first-child {
			margin-right: 20px; }

		&:nth-child(3) {
			margin: 0 20px;
			img {
				height: 100%; }
			span {
				display: block;
				position: absolute;
				bottom: 20px;
				left: 0;
				padding: 0 15px; } }

		.gradient {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: linear-gradient(to top, rgba(255, 255, 255, 0.9) 25%, rgba(0, 0, 0, 0.1) 65%); } } }

.news__row__third {
	display: flex;
	justify-content: space-between;
	margin-top: 20px;
	font-size: 12px;
	font-weight: 500;
	color: $blue;

	.news__left__block {
		flex: 5 1 50%;
		margin-right: 20px;

		.news__block {
			height: 100%;
			position: relative;
			background-color: #fff;
			img {
				width: 100%;
				height: 100%; }
			span {
				display: block;
				position: absolute;
				bottom: 50px;
				left: 0;
				padding: 0 15px; }
			&:first-child {
				display: none; } }

		.gradient {
			position: absolute;
			width: 100%;
			height: 100%;
			background: linear-gradient(to top, rgba(255, 255, 255, 0.9) 20%, rgba(0, 0, 0, 0.1) 55%); } }

	.news__right__block {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		flex: 1 1 50%;

		.news__block {
			position: relative;
			background-color: #fff;
			width: 100%;
			height: 30%;
			img {
				width: 35%; }
			span {
				display: block;
				padding: 10px 15px 0;
				margin-bottom: 30px; } }

		.news__block {
			display: flex; } } }

.news__row__four {
	display: flex;
	justify-content: space-between;
	margin-top: 20px;
	font-size: 12px;
	font-weight: 500;
	color: $blue;

	.news__left__block {
		position: relative;
		flex: 3 1 22%;
		background-color: #fff;
		img {
			width: 100%; }
		span {
			display: block;
			padding: 8px 20px 0;
			margin-bottom: 40px; } }


	.news__center__block {
		flex: 1 1 55%;
		margin: 0 20px;
		position: relative;
		background-color: #fff;
		img {
			width: 100%;
			height: 100%; }
		span {
			display: block;
			position: absolute;
			bottom: 50px;
			left: 0;
			padding: 0 15px; } }

	.gradient {
		position: absolute;
		width: 100%;
		height: 100%;
		background: linear-gradient(to top, rgba(255, 255, 255, 0.9) 20%, rgba(0, 0, 0, 0.1) 55%); }

	.news__right__block {
		position: relative;
		flex: 1 1 22%;
		background: #fff;
		img {
			width: 100%; }
		span {
			display: block;
			padding: 8px 20px 0;
			margin-bottom: 40px; } } }

.button__loading {
	display: flex;
	justify-content: center;

	.loading__more {
		text-align: center;
		margin: 45px 0 40px;
		padding: 3px 15px;
		background: none;
		border: 1px solid $blue;
		border-radius: 15px;
		color: $blue;
		transition: background 0.3s, border 0.3s, color 0.3s;
		&:hover {
			border: 1px solid $blue;
			background: $blue;
			color: #fff; } } }
